<template>
  <div class="AllBox">
    <el-dialog
      title="添加角色"
      :visible.sync="dialogVisible"
      width="30%" center>
      
      <el-form label-width="0px" class="login_in"  ref="dataForm" :model="dataForm">

<div class="form-info">
  <el-form-item prop="name">
    <el-input
      type="text"
      prefix-icon="el-icon-user"
      placeholder="请输入添加的角色名字"
      v-model.trim="dataForm.name">
    </el-input>
  </el-form-item>
  <el-form-item prop="roleKey">
    <el-input
    prefix-icon="el-icon-user"
      placeholder="请输入添加角色的别名"
      v-model.trim="dataForm.roleKey"
    ></el-input>
  </el-form-item>
</div>

</el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="closeDialogVisible">取 消</el-button>
      <el-button type="primary" @click="showDialogVisible(),giveFather()">确 定</el-button>
    </span>
</el-dialog>
  </div>
</template>

<script>
  export default {
    name:'addRolepopUp',
    data() {
      return {
        dataForm:{
          name:'',
          roleKey:'',
          roleId:6,
        },
        dialogVisible: false
      };
    },
    methods:{
      closeDialogVisible(){
        this.dialogVisible = false
      },
      showDialogVisible(){
         this.dialogVisible = !this.dialogVisible
      },
      giveFather(){
        this.$emit('giveFather',this.dataForm)
      }
    }
  };
</script>
